.Navbar_btn__37eOQ, .Navbar_btn__37eOQ:before, .Navbar_btn__37eOQ:after {
    display: block;
    width: 50px;
    height: 4px;
    background: gray;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -25px;
    margin-top: -2px;
    transition: all 0.7s linear;
}
.Navbar_btn__37eOQ:before, .Navbar_btn__37eOQ:after {
    content: '';
}
.Navbar_btn__37eOQ:before {
    transform: translateY(-15px)
}
.Navbar_btn__37eOQ:after {
    transform: translateY(16px)
}
.Navbar_btn_active__3udef:before, .Navbar_btn_active__3udef:after {
    content: '';
}
.Navbar_btn_active__3udef, .Navbar_btn_active__3udef:before, .Navbar_btn_active__3udef:after {
    width: 60px;
    display: block;
    height: 4px;
    background: gray;
    position: absolute;
    top: 50%;
    left: 25%;
    margin-left: -5px;
    margin-top: -2px;
    transition: all 0.7s ease-in-out;
}
.Navbar_btn_active__3udef {
 
    transform: rotate(-45deg);
    transform-origin: center;
    background:gray;
    top: 50%;
    left: 25%;
    margin-left: 0;
    margin-top: 0;
}

.Navbar_btn_active__3udef:before {

    transform: rotate(90deg);
    transform-origin: center;
    margin-left: -15px;
    margin-top: -2px;
}
 .Navbar_btn_active__3udef:after {
     opacity: 0;
    width: 30px;
    transform: rotate(35deg);
    transform-origin: right top;
} 


